.dropZone {
    width: 100%;
    height: 200px;
    border: 1px dashed black;
    margin-top: 15px;
    margin-bottom: 5px;
}

.dropZone:hover {
    cursor: pointer;
}

.dropzoneLabel {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}